@import "src-doc/doc.global.scss";
















































































































































































































































































@include themify {
    .pl-date-service {
        .plain-popper-content {
            padding: 0 !important;

            & > .pl-date-base-panel {
                border: none;
            }
        }
    }
    .pl-date {
        .pl-date-inner {
            display: flex;
            align-items: stretch;
            height: 100%;

            & > span {
                color: $icc;
            }

            .pl-date-input-inner {
                border: none;
                outline: none;
                width: 90px;
                height: 100%;
                background: transparent;
            }
        }

        &.pl-date-range {
            .pl-date-input-inner {
                text-align: center;
            }
        }
    }
}
