@import "src-doc/doc.global.scss";

















































































































@include themify {
    .pl-time-base-column {

        $item_height: 24px;

        display: inline-block;
        height: 7*$item_height;
        width: 60px;
        border: solid $ibc 1px;
        border-radius: 2px;

        .pl-time-base-column-list {
            margin: 0;
            padding: 0;
            list-style: none;
            position: relative;

            .pl-time-base-column-item {
                font-size: 12px;
                height: $item_height;
                line-height: $item_height;
                transition: all 300ms $transition;
                text-align: center;

                &.pl-time-base-column-option-item {
                    cursor: pointer;

                    &:hover {
                        background-color: rgba($colorInfo, 0.1);
                        color: $ihc;
                    }
                }

                &.pl-time-base-column-item-disabled {
                    color: $disabledText;
                    cursor: not-allowed;

                    &:hover {
                        color: $disabledText;
                        background-color: transparent;
                    }
                }

                &.pl-time-base-column-item-current {
                    background-color: rgba($colorPrimary, 0.1);
                    color: $colorPrimary;

                    &:hover {
                        color: $colorPrimary;
                    }
                }
            }
        }
    }
}
