@import "src-doc/doc.global.scss";
































































































































































































































@include themify {
    .pl-cascade-panel {
        display: inline-block;
        height: 200px;
        border: solid 1px $ibc;
        border-radius: $popperRadius;

        & > .pl-list {
            height: 100%;
            overflow: hidden;
        }

        .pl-cascade-list {
            width: 180px;
            height: 100%;
            padding: 6px 0;
            font-size: 14px;
            display: inline-block;
            vertical-align: top;

            &:not(:last-child) {
                border-right: solid 1px $ibc;
            }

            .pl-list {
                overflow: hidden;
            }

            .pl-cascade-item {
                padding: 4px 6px 4px 16px;
                transition: all 300ms $transition;
                cursor: pointer;

                .pl-cascade-content {
                    padding-right: 30px;
                    position: relative;

                    .pl-cascade-arrow {
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        right: 0;
                        width: 30px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        color: $icc;
                    }
                }

                &.pl-cascade-item-expand, &:hover {
                    color: $ihc;
                    background-color: rgba($colorInfo, 0.1);
                }

                &.pl-cascade-item-active {
                    color: $colorPrimary;
                    background-color: rgba($colorPrimary, 0.1);
                }

                &.pl-cascade-empty {
                    padding-right: 0;
                    padding-left: 0;
                    text-align: center;
                    font-size: 12px;
                    color: $disabledText;
                    background-color: transparent;
                    cursor: auto;

                    .pl-icon {
                        margin-right: 3px;
                    }
                }

                &.pl-cascade-item-disabled {
                    background-color: transparent;
                    color: $disabledText;
                    cursor: not-allowed;
                }
            }

            &.pl-cascade-filter-list {
                width: 360px;
            }
        }
    }

    .pl-cascade-service-popper {
        .plain-popper-content {
            padding: 0 !important;
        }

        .pl-cascade-panel {
            height: 100%;
            border: none !important;
        }
    }

    .pl-cascade {
        .pl-input-suffix-icon .pl-icon {
            transform: rotate(90deg);
            transition: transform 300ms $transition;
        }

        &.pl-cascade-open {
            .pl-input-suffix-icon .pl-icon {
                transform: rotate(-90deg);
            }
        }
    }

    .pl-loading-mask {
        top: 1px;
        bottom: 1px;
        left: 1px;
        right: 1px;
    }
}

